    left: 0;
    top: 0;
    animation: hero-logo-circle 1s linear infinite;
    will-change: transform;
    user-select: none;
}
.hero-logo-circle:nth-child(1) {
    animation-duration: 30s;
}
.hero-logo-circle:nth-child(2) {
    animation-duration: 40s;
}
.hero-logo-circle:nth-child(3) {
